import React, { Component } from 'react';
import { StyleSheet, Text, TextInput, View, Button, Alert, KeyboardAvoidingView, TouchableOpacity, AsyncStorage, FlatList} from 'react-native'
import firebase from 'firebase'
import 'firebase/firestore';
import { SplashScreen } from 'expo';
import { ListItem } from 'react-native-elements'

export default class CompScreen extends React.Component {

      state = {
        teams:[]
      }
    
    constructor(props){
      super(props)
      let symbolBtns = ['ðŸ”µ', 'ðŸŸ¢', 'ðŸŒŸ'];
      let name = this.props.navigation.getParam('name', 'No Name')
      let sku = this.props.navigation.getParam('sku','sku')
      console.log(sku)
      let fireStSn;
        AsyncStorage.getItem('team').then((team)=>{
            this.setState({team})
            fetch('https://api.vexdb.io/v1/get_teams?season=current&sku='+sku)
            .then((response) => response.json())
            .then((responseJson)=>{
                let teams = responseJson["result"]
                AsyncStorage.setItem('offlineTeamData-'+sku,JSON.stringify(teams))
                this.setState({teams})            
                fireStSn = firebase.firestore().collection('teams').doc(team.replace(/\D+/g, '')).collection('comp').doc(sku).collection('teams').onSnapshot(snapshot => {
                  for(let teamItemNa in teams){
                    let teamItem = teams[teamItemNa]
                    let has = false;
                    let symbol;
                    snapshot.forEach(doc => {
                        //console.log(doc.id, '=>', doc.data());
                        if(doc.id == teamItem.number){
                            if(Object.keys(doc.data()).length>10){
                              console.log("AHHH")
                              has = true;
                            }
                            if(doc.data() && doc.data()['Symbol']!=null && team.toLowerCase() == "750s")
                              symbol = symbolBtns[doc.data()['Symbol']]
                        }
                    });
                    teams[teamItemNa].haveData = has;
                    teams[teamItemNa].symbol = symbol;
                  }
                  AsyncStorage.setItem('offlineTeamData'+sku,JSON.stringify(teams))
                  this.setState({teams}) 
                })
                console.log(team+"1")

                fetch('https://api.vexdb.io/v1/get_matches?season=current&sku='+sku+'&team='+team)
                .then((response) => response.json())
                .then((responseJson)=>{
                  console.log('https://api.vexdb.io/v1/get_matches?season=current&sku='+sku+'&team='+team)
                    let matches = responseJson["result"]
                    //console.log(matches)
                    let allies = []
                    let opps = []
                    if(matches.length>0){
                        for(let matchNa in matches){
                          let matchItem = matches[matchNa]
                          let ourTeamColor;
                          let oppTeamColor
                          let matchNum = matchItem.matchnum;
                          let matchType = matchItem.round=="1"?"Practice":matchItem.round=="2"?"Quals":matchItem.round=="3"?"Quarter Finals":matchItem.round=="4"?"Semi Finals":matchItem.round=="5"?"Finals":"Unknown";
                          let matchTypeNum = Number(matchItem.round)
                          if(matchItem.red1==team||matchItem.red2==team||matchItem.red3==team){
                            ourTeamColor = "red"
                            oppTeamColor = "blue"
                          }else if(matchItem.blue1==team||matchItem.blue2==team||matchItem.blue3==team){
                            ourTeamColor = "blue"
                            oppTeamColor = "red"
                          }
                          let matchTeams = [];
                          for(var i = 1; i!=4;i++)
                            if(matchItem[ourTeamColor+i]!=team){
                              allies.push(matchItem[ourTeamColor+i])
                              matchTeams.push(matchItem[ourTeamColor+i])
                            }
                          for(var i = 1; i!=4;i++){
                              opps.push(matchItem[oppTeamColor+i])
                              matchTeams.push(matchItem[oppTeamColor+i])
                          }
                          for(let teamItemNa in teams){
                            let teamItem = teams[teamItemNa]
                            for(let matchedTeam of matchTeams)
                              if(matchedTeam == teamItem.number){
                                teams[teamItemNa].round = matchNum;
                                teams[teamItemNa].type = matchType;
                                teams[teamItemNa].typeNum = matchTypeNum;
                              }
                          }
                        }
                        for(let teamItemNa in teams){
                          let teamItem = teams[teamItemNa]
                          let hasAlly = false;
                          let hasOpp = false;
                          for(let ally of allies)
                            if(ally == teamItem.number)
                              hasAlly = true
                          for(let opp of opps)
                            if(opp == teamItem.number)
                              hasOpp = true
                          teams[teamItemNa].ally = hasAlly;
                          teams[teamItemNa].opp = hasOpp;
                          //teams[teamItemNa].round = matchItem.round
                        }
                        teams.sort((a,b)=>{
                          let typeA = a.typeNum?a.typeNum:-1;
                          let typeB = b.typeNum?b.typeNum:-1;
                          if(typeB-typeA==0){
                            let matchA = a.round?a.round:-1;
                            let matchB = b.round?b.round:-1;
                            return matchA-matchB
                          }
                          return typeB-typeA
                        })
                        console.log("done sorting")
                        AsyncStorage.setItem('offlineTeamData'+sku,JSON.stringify(teams)) //save the data offline for later
                        this.setState({teams}) //send new team information to app interface
                      }
                    })
                  })

              AsyncStorage.getItem('offlineTeamData'+sku).then((offlineTeamData)=>{
              
                if(offlineTeamData){
                  console.log("\n\n\n\n\nOFF\n\n\n\n\n")
                    let teams = JSON.parse(offlineTeamData)
                    this.setState({teams})            
                    fireStSn = firebase.firestore().collection('teams').doc(team.replace(/\D+/g, '')).collection('comp').doc(sku).collection('teams').onSnapshot(snapshot => {
                        for(let teamItemNa in teams){
                            let teamItem = teams[teamItemNa]
                            let has = false;
                            let symbol;
                            snapshot.forEach(doc => {
                                //console.log(doc.id, '=>', doc.data());
                                if(doc.id == teamItem.number){
                                  if(Object.keys(doc.data()).length>10){
                                    console.log("AHHH")
                                    has = true;
                                  }
                                  if(doc.data() && doc.data()['Symbol'])
                                    symbol = symbolBtns[doc.data()['Symbol']]
                                }
                            });
                            teams[teamItemNa].haveData = has;
                            teams[teamItemNa].symbol = symbol;
                        }
                        this.setState({teams}) 
                    })
                   }
  
                  })            
  
                })
      
    }

    static navigationOptions = ({ navigation }) => {
        return {
          title: navigation.getParam('title', 'No Name').substring(0,25)+`${navigation.getParam('title', 'No Name').substring(25).trim()!=""?'...':''}`,
        };
      };
   
    render() {
      return (
        <FlatList
            data={this.state.teams}
            renderItem={({ item }) => <ListItem chevron checkmark={item.haveData} onPress={()=>this.props.navigation.navigate('TeamInfo',{team:item.number, compName: this.props.navigation.getParam('compName'),sku:this.props.navigation.getParam('sku')})}
            bottomDivider topDivider subtitle={item.organisation+" - "+item.region} title={(item.symbol?item.symbol+" ":"")+item.number+(item.round?" - "+item.round:"")+(item.type?" - "+item.type:"")} titleStyle={item.ally&&item.opp?{ color: 'purple', fontWeight: 'bold' }:item.ally?{ color: 'green', fontWeight: 'bold' }:item.opp?{ color: 'red', fontWeight: 'bold' }:null}/>}
            keyExtractor={item => item.id}
            ListEmptyComponent={<View><Text>No Competitions found for {this.state.team}</Text></View>}
        />
      )
    }

}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor:"red",
    
  },
  textInputBox: {
    borderBottomColor:"white",
    borderBottomWidth: 1,
    marginBottom: 40,
    width:"90%",
  },
  textInput: {
    alignSelf: 'stretch',
    fontSize: 24,
    height: 40,
    fontWeight: '200',
    marginBottom: 0,
    color:"white",
    
  },
  title: {
    fontSize:50,
    marginBottom:30,
    color:"white"
  },
  submitButton:{
    alignItems: 'center',
    backgroundColor:"blue",
    width:"90%",
    padding:15,
    borderRadius:7,
    marginBottom:10
  },
})